<template>
  <div id="arcontainer">
    <vue-scroll :ops="ops">
      <div id="articlebox">
        <div class="titlebox">
          <div class="decorated"></div>
          <div class="title" v-html="content.title"></div>
          <div id="arother">
            <span>
              <i class="el-icon-view"></i>
              {{views}}
            </span>
            <span>
              <i class="el-icon-s-grid"></i>
              {{this.category[0]}}
            </span>
            <span>
              <i class="el-icon-date"></i>
              {{content.date}}
            </span>
          </div>
        </div>
        <div class="markdown-body" v-html="content.content"></div>
      </div>
    </vue-scroll>
  </div>
</template>
<script>
import { GetArticle } from "../../api/index";
export default {
  data() {
    return {
      category: [],
      content: {},
      views: null,
      ops: {
        //vuescroll的配置
        vuescroll: {
          sizeStrategy: "percent",
          detectResize: true
        },
        scrollPanel: {},
        rail: {},
        bar: {
          showDelay: 500,
          onlyShowBarOnScroll: false,
          keepShow: false,
          background: "#b3c0d1",
          opacity: 1,
          hoverStyle: false,
          specifyBorderRadius: false,
          minSize: false,
          size: "6px",
          disable: false
        }
      }
    };
  },
  created() {
    this.getarticle();
  },
  watch: {
    // 监听路由参数改变来解决搜索文章点击搜索结果，页面无法更新的问题
    "$route": function(newVal, oldVal) {
      this.getarticle();
    }
  },
  methods: {
    getarticle() {
      GetArticle(this.$route.query).then(res => {
        console.log(this.$route.query, res);
        this.content = res.data[0];
        this.category = this.content.category;
        this.views = this.content.views;
      });
    }
  }
};
</script>
<style lang="scss">
#arcontainer {
  width: 100%;
  height: 100%;
}
#articlebox {
  margin: 20px 30px;
  background: white;
  padding: 10px 80px;
  .title {
    height: 50px;
    line-height: 50px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
  }
  .titlebox {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .decorated {
    position: absolute;
    height: 80px;
    width: 5px;
    top: 0px;
    left: 30px;
    background: rgb(32, 121, 173);
  }
  #arother {
    span {
      height: 50px;
      line-height: 50px;
      font-size: 16px;
    }
  }

  #arother > span {
    margin-left: 20px;
  }
  img {
    margin-top: 10px;
    max-width: calc(100% - 160px);
  }
}
</style>
